@import "../../styles/AppDesign.scss";
.splash{
    position: fixed;
    height: 100vh;
    width: 100%;
    z-index: 800;
    background: $bgWhite;
    top:0;
    left:0;
    transition: .4s;
    .page-ready &{
        background: transparent;
        pointer-events: none;
    }
}
.splash__inner{
    position: absolute;
    height: 100%;
    width: 100%;
    right:0;
    top:0;
    transform-origin: right top;
    display:flex;
    align-items: center;
    justify-content: center;
    .splash--step-3 &{
        $logoStaticSize:157.16px;
        $logoStaticSizeM:97.16px;
        @keyframes logo-move {
            0%   { transform: scale(1); }
            75%   { transform: scale(1); }
            100% { height:$logoStaticSize * 0.8; width: $logoStaticSize; margin-right:5px;}
        }        
        animation: logo-move $ani-time-splash-step3 forwards;
        @include tabletView(){
            @keyframes logo-move-mb  {
                0%   { transform: scale(1); }
                75%   { transform: scale(1); }
                100% { height:$logoStaticSizeM * 0.8; width: $logoStaticSizeM; margin-right:5px;}
            }        
            animation: logo-move-mb $ani-time-splash-step3 forwards;
        }
    }
}

.splash__logo{
    width:70%;
    max-width: 640px;
    transform: scale(1);
    .splash--step-0 &{
        opacity:0;
    }
    .splash--step-1 &{
        opacity:1;
        @keyframes logo-enlarge {
            0%   { transform: scale(0.25); }
            100% { transform: scale(1);  }
        }        
        animation: logo-enlarge $ani-time-splash-step1;
    }
    .splash--step-2 &{
        opacity:1;
    }
    .splash--step-3 &{
        opacity:1;
    }
    .color-inveresed.splash-finished &{
        > *{
            transition:.1s;
            transition-delay: .4s;
            opacity:0;
            &.splash__static-logo{
                opacity:1;
            }
        }
    }
}

.splash__static-logo{
    position: absolute;
    width: 100%;
    height:100%;
    top:0;
    left:0;
    background:url('/img/logo/logo-main-white.svg') no-repeat center center;
    background-size:contain;
    opacity:0;
    .color-inveresed &{
        background-image:url('/img/logo/logo-main-white-text.svg');
    }
}