
// Fonts Size:
$fontSizeTitleXXL:10rem;
$fontSizeTitleXL:6.5rem;
$fontSizeTitleL:4rem;
$fontSizeTitleM:3rem;
$fontSizeTitleS:2.5rem;
$fontSizeTitleSS:1.3rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:2rem;
$fontSizeL:1.5rem;
$fontSizeM:1.3rem;
$fontSizeS:1rem;
$fontSizeSS:.9rem;
$fontSizeXS:.5rem;

// 20201226 - tried to use px for most type settings to be accurate

// Fonts
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Regular", normal, 200);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Semibold", normal, 400);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Bold", normal, 600);


$fontPrimary: "Myriad Pro", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

$fontSecondary: $fontPrimary; // TODO: remove if not in use

@mixin secTitleFontSize() {
    font-size:100px;
    @include largeScreen(){
        // font-size:$fontSizeTitleL + 1.5;
        font-size:70px;
    }
    @include tabletView(){
        // font-size:$fontSizeTitleL;
        font-size:52px;
    }
    @include mobileView(){
        // font-size:$fontSizeTitleM;
        font-size:38px;
    }
}

@mixin secTitle() {
    text-transform: uppercase;
    line-height: 1;
    @include secTitleFontSize();
    width:125%;
    &-icon{
        display: block;
        $size:60px;
        width: $size;
        height: $size;
        margin-bottom:1.5rem;
        background: url('/img/gallery/icon-gallery.svg') no-repeat center center;
        background-size: contain;
        @include mobileView(){
            $size:40px;
            width: $size;
            height: $size;
        }
    }
    &-txt{
        letter-spacing: 0.02em;
    }
    &-tag{
        // font-size:$fontSizeL;
        font-size:20px;
        line-height: 1.2;
        letter-spacing: 1px;
        margin-bottom:1rem;
    }
    &-gray{
        opacity: .1;
    }
    &-tag,
    &-txt{
        display:block;
    }
}
@mixin secDesc() {
    // font-size:$fontSizeM;
    font-weight: 400;
    font-size: 24px;
    letter-spacing: 0;
    line-height: 34px;
    @include mobileView(){
        font-size: 18px;
        line-height: 26px;
    }
}

@mixin secIndicatorTypo(){
    font-size: 15px;
    letter-spacing: 1px;
    line-height: 24px;
    
}

@mixin specTypo(){
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 15px;
    line-height: 20px;
}

@mixin exhibitionTitleTypo {
    font-weight: 400;
    font-size: 40px;
    line-height: 44px;
}
@mixin exhibitionDetailTypo {
    font-weight: 400;
    font-size: 30px;
    line-height: 36px;
}

@mixin specBigTypo(){
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 18px;
    line-height: 28px;
}

@mixin specDescTypo(){
    font-weight: 400;
    letter-spacing: 1px;
    font-size: 15px;
    line-height: 20px;
}