@import "../../styles/AppDesign.scss";

.shapeout-text {
    position: relative;
    width: 100%;
}

.shapeout-text__body {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
}

.shapeout-text__line {
    position: relative;
    display: block;
    font-size: 0;
}

.shapeout-text__content {
    position: relative;
    display: inline-block;
    overflow: hidden;

    >* {
        opacity: 0;
        // transform: translateX(100%);
        z-index: 10;
        @include secTitleFontSize();
        // opacity: 1;
    }
}

.shapeout-text__shape {
    position: absolute;
    display: block;
    overflow: hidden;
    width: 100%;
    height: 90%;
    top: 5%;
    transform: skewX(15deg);
    opacity: 1;
    z-index: 20;

    &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 70vw;
        height: 100%;
        background: rgb(216, 24, 42);
        background: linear-gradient(90deg, rgba(255, 255, 255, 0) 2%, rgba(216, 24, 42, 1) 20%, rgba(216, 24, 42, 1) 80%, rgba(255, 255, 255, 0) 98%);
        // transform: scaleY(1) translateX(-50%);
        transform-origin: center center;
        transform: scaleY(1) translateX(-100%);
        // @keyframes shape-in {
        //     0%   {
        //         transform: scaleY(1) translateX(-100%);
        //     }
        //     100% {
        //         transform: scaleY(0) translateX(100%);
        //     }
        // }  
    }
}

.shapeout-text.is-active {
    .shapeout-text__shape:before {
        transition: 1.5s;
        transform: scaleY(0) translateX(100%);

    }

    .shapeout-text__content {
        >* {
            transition: .3s .5s;
            opacity: 1;
            transform: translateX(0);
        }
    }

    .shapeout-text__line:nth-child(2) {
        .shapeout-text__shape:before {
            transition-delay: .3s;
        }

        .shapeout-text__content>* {
            transition-delay: .8s;
        }
    }
}